html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Nunito", sans-serif;
  background-color: var(--color-white);
  font-weight: 400;
  font-size: 15px;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
  height: 300vh;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1440px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.btn-main {
  padding: 15px 25px;
  border-radius: 50px;
  background-color: var(--color-orange);
  color: var(--color-white);
  text-decoration: none;
}

.header__menu {
  padding: 10px 35px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--color-white);
  box-shadow: 0 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 0 0 30px 30px;
}

.header__logo {
  user-select: none;
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--color-dark);
}

.nav__list {
  display: flex;
}

.nav__item {
  margin-left: 35px;
}

.hero {
  min-height: 500px;
}

.hero-main {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.hero-main__title {
  font-family: "Rubik", sans-serif;
  font-size: 2.5rem;
  color: var(--color-dark);
  font-weight: 500;
  margin: 0;
  margin-bottom: 25px;
}

.hero-main__list {
  padding: 0;
  margin: 0;
  font-size: 1.22rem;
  color: var(--color-dark);
}

.hero-main__item {
  display: flex;
  align-items: center;
}

.hero-main__item:not(:last-child) {
  margin-bottom: 15px;
}

.hero-main__icon {
  width: 27px;
  height: 27px;
  margin-right: 8px;
  fill: var(--color-orange);
}

.hero-main__shef {
  display: flex;
  position: relative;
  max-width: 700px;
  padding: 20px;
  overflow: hidden;
  border-radius: 30px;
  box-shadow: 0 0px 25px rgba(0, 0, 0, 0.2);
  animation: shefAnim 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

.hero-main__shef::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 120%;
  height: 120%;
  background-color: white;
  filter: blur(15px);
  opacity: 0.3;
}

.hero-main__shef-img {
  width: 50%;
}

.hero-main__shef-about {
  width: 50%;
  padding: 30px 20px;
}

.hero-main__shef-name {
  font-weight: 600;
  font-size: 1.34rem;
}

.hero-slider {
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.swiper-slide {
  height: 100%;
}

@keyframes shefAnim {
  0% {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
  }
  100% {
    -webkit-transform: translateZ(50px);
    transform: translateZ(50px);
    -webkit-box-shadow: 0 0px 25px rgba(0, 0, 0, 0.2);
    box-shadow: 0 0px 25px rgba(0, 0, 0, 0.2);
  }
}
